<template>
  <div class="editor">
    <editor-menu-bubble :editor="editor" :keep-in-bounds="keepInBounds" v-slot="{ commands, isActive, menu }">
      <div
        class="menububble"
        :class="{ 'is-active': menu.isActive }"
        :style="`left: ${menu.left}px; bottom: ${menu.bottom}px;`"
      >

        <button
          class="menububble__button"
          :class="{ 'is-active': isActive.bold() }"
          @click="commands.bold"
        >
          <feather-icon icon="BoldIcon" class="menububble__icon" />
        </button>

        <button
          class="menububble__button"
          :class="{ 'is-active': isActive.italic() }"
          @click="commands.italic"
        >
          <feather-icon icon="ItalicIcon" class="menububble__icon" />
        </button>

      </div>
    </editor-menu-bubble>

    <editor-content class="editor__content" :editor="editor" />
  </div>
</template>

<script>
import { Editor, EditorContent, EditorMenuBubble } from 'tiptap'
import { Heading, Bold, Italic } from 'tiptap-extensions'

export default {
  components: {
    EditorContent,
    EditorMenuBubble
  },
  data() {
    return {
      keepInBounds: true,
      editor: new Editor({
        extensions: [
          new Heading({ levels: [1, 2, 3] }),
          new Bold(),
          new Italic()
        ],
        content: `
          	<p>Hi, Kellie. We can offer you:</p>
			<p><br></p>
			<p>All in: <strong>2000$</strong></p>
			<p>Miles out: <strong>15</strong></p>
			<p>Time to pick up: <strong>10h22 min</strong></p>
			<p>Vehicle Truck type: <strong>CARGO VAN</strong></p>
			<p>Dimensions: <strong>200x300x100 in</strong></p>
			<p>MC <strong>#1112233</strong></p><br>
			<p><br></p>
			<p>Looking forward to your respond. Kind regards, John</p>
		`,
		onUpdate: ({ getHTML }) => {
			console.log(getHTML())
		}
      })
    }
  },
  beforeDestroy() {
    this.editor.destroy()
  }
}
</script>


<style lang="scss">
.editor{
	width: 100%;
	background: #ffffff;
	border: 1px solid #CECECE;
	border-radius: 4px;
	padding: 25px;
}

.menububble {
	position: absolute;
	display: flex;
	z-index: 20;
	background: #7ABDFF;
	border-radius: 5px;
	padding: 0.3rem;
	margin-bottom: 0.5rem;
	transform: translateX(-50%);
	visibility: hidden;
	opacity: 0;
	transition: opacity 0.2s, visibility 0.2s;

	&.is-active {
		opacity: 1;
		visibility: visible;
	}

	&__icon{
		svg{
			width: 16px;
			height: 16px;
		}
	}

	&__button {
		display: inline-flex;
		background: transparent;
		border: 0;
		color: #fff;
		padding: 0.2rem 0.5rem;
		margin-right: 0.2rem;
		border-radius: 3px;
		cursor: pointer;

		&:last-child {
			margin-right: 0;
		}

		&:hover {
			background-color: rgba(#fff, 0.1);
		}

		&.is-active {
			background-color: rgba(#fff, 0.2);
		}
	}

	&__form {
		display: flex;
		align-items: center;
	}

	&__input {
		font: inherit;
		border: none;
		background: transparent;
		color: #fff;
	}
}

</style>